@media screen and (max-width: 1685px) {

  .suggestions {
    display: none;
  }

}

@media screen and (max-width: 1330px) {

  .navbar {
    top: 0;
    display: flex;
    align-items: center;
    object-fit: contain;
    gap: 5%;
  }

  .nav_headerWrapper {
    max-width: 100%;
    margin: 0 auto;
    width: 100%;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .instagram-logo {
    max-width: 100px;
    height: 30px;
  }

  .icons {
    flex-direction: row;
    padding-left: 24px;
    padding-top: 1%;
    padding-bottom: 1%;
  }

  .icons a {
    max-width: 30%;
    text-align: center;
    padding: 0 10px;
    transition: all 0.3s ease;
    font-size: 24px;
  }

  .mainclass {
    display: grid;
    grid-template-columns: 60% 30%;
    margin-left: 15px;
  }

  .feed {
    width: 60%;
  }

  .feed-carroussel {
    max-width: 500px;
  }

  .feed-post {
    border-width: 0px;
    width: 100%;
    box-sizing: unset;
    padding-left: 4px;
  }

  .suggestions {
    display: none;
  }

}

@media screen and (max-width: 650px) {

  .nav_headerWrapper {
    max-width: 100%;
  }

  .searchbar {
    display: none;
  }

  .feed-fotos {
    max-width: 500px;
  }

  .feed-post {
    border-width: 0px;
    max-width: 75%;
    box-sizing: unset;
    padding-left: 4px;
  }

}

@media screen and (max-width: 550px) {

  .searchbar {
    display: none;
  }

  .feed-fotos {
    max-width: 400px;
  }

  .feed-post {
    border-width: 0px;
    box-sizing: unset;
    padding-left: 4px;
  }

}

@media screen and (max-width: 450px) {

  .searchbar {
    display: none;
  }

  .feed-fotos {
    max-width: 350px;
  }

  .icons {
    color: #000;
    display: flex;
    flex-direction: row;
    padding-left: 5px;
    padding-top: 1%;
    padding-bottom: 1%;
  }

  .icons a {
    max-width: 30%;
    padding: 0 5px;
    font-size: 20px;
  }

}