* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif;
}

body {
  color: #262626;
  font-size: 14px;
  line-height: 18px;
}

a {
  text-decoration: none;
  color: inherit;
}

/* ---------- BEGIN - NAV BAR ---------- */
.navbar {
  position: sticky;
  position: fixed;
  top: 0;
  z-index: 9999;
  height: 55px;
  display: flex;
  align-items: center;
  object-fit: contain;
  border-bottom: 1px solid lightgray;
  gap: 15%; /* espa√ßo entre logo, search e icons */
  margin: 0 auto;
  width: 100%;
  padding: 0 20px;
  justify-content: space-between;
}


.instagram-logo-div {

}


.instagram-logo {
  max-width: 100px;
  height: 30px;
  cursor: pointer;
}

.searchbar {
  color: #8E8E8E;
  padding-top: 3%;
  padding-bottom: 3%;
  font-size: 16px;

}


/* ---------- BEGIN - ICON BAR ---------- */
.icon-bar {
  color: #000;
  display: flex;
  flex-direction: row;
  padding-left: 24px;
  padding-top: 1%;
  padding-bottom: 1%;
}

.icon-bar a {
  max-width: 30%;
  text-align: center;
  padding: 0 10px;
  transition: all 0.3s ease;
  color: #000000;
  font-size: 24px;
}

.icon-bar a:hover {
  color: #840058;
}


/* ---------- END - ICON BAR ---------- */
.mainclass {
  display: grid;
  grid-template-columns: 60% 30%;
  margin-left: 20px;
  justify-content: center;
}

.feed {
  width: 60%;
}

.feed-carroussel {
  width: 500px;
}

.feed-fotos {
  max-width: 500px;
  height: 500px;
  margin-bottom: 10px;
  margin-top: 10px;  
}

.feed-post {
  border-width: 1px grey;
  border-style: outset;
  width: 505px;
  box-sizing: unset;
  padding-left: 4px;
}

.suggestions {
  max-width: 300px; 
  position: fixed;
  margin-right: 50%;
}

.prof-photo-sugest {
  margin-top: 70px;
  max-width: 300px;
  max-height: 60px;
}

.carousel-img {
  margin-top: 70px;
  max-width: 300px;
  max-height: 60px;
}

.box-filme {
  margin-top: 75px;
  height: 65px;
  width: 65px;
  border-radius: 50%;
  border: 3px rgb(255, 20, 235);
  border-style: outset;
}

.carrossel-titulo {
  padding: 0px 10px 10px 0px;
}

.carrossel-filmes {
  margin-top: 5px;
  cursor: pointer;
}

.footer {
  margin-top: 60%;
  max-width: 300px;
}

.footer-list ul li {
  list-style: none;
  display: inline-block;
  padding: 1px;
  font-size: 12px;
  color: #C7C7C7;
}

.rightsfooter {
  padding-top: 15px;
  font-size: 12px;
  color: #C7C7C7;
}