* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
        Arial, sans-serif;
}

body {
    color: #262626;
    font-size: 14px;
    line-height: 18px;
}

a {
    text-decoration: none;
    color: inherit;
}

/* ---------- BEGIN - NAV BAR ---------- */
.navbar {
    position: sticky;
    position: fixed;
    top: 0;
    z-index: 9999;
    background-color: #ffffff;
    height: 55px;
    display: flex;
    align-items: center;
    border-bottom: 2px solid lightgray;
    width: 100%;
    gap: 15%;
    margin: 0 auto;
    padding-inline-end: 5%;
    justify-content: space-around;
}


.instagram-logo-div {
    background-color: #ffffff;
    margin-left: 25%;
}


.instagram-logo {
    max-width: 150px;
    height: 40px;
    background-color: #ffffff;
    cursor: pointer;
    padding-top: 3%;
}

.searchbar {
    padding-bottom: 5%;
    font-size: 25px;
    max-width: 300px;
    max-height: 35px;
}

/* ---------- BEGIN - ICON BAR ---------- */
.icon-list {
    list-style: none;
    display: flex;
    padding: 5px;
    font-size: 25px;
    color: #000000;
}

.icons {
    color: #000;
    padding-top: 1%;
    padding-bottom: 1%;
    max-width: 600px;
    margin-right: 10%;
}

.icons a {
    max-width: 30%;
    text-align: center;
    padding: 0 10px;
    transition: all 0.3s ease;
    color: #000000;
    font-size: 25px;
}

.icons a:hover {
    background-color: #ffffff;
    color: #b30077;
}
/* ---------- END - ICON BAR ---------- */

.mainclass {
    display: grid;
    grid-template-columns: 60% 30%;
    justify-content: center;
}

.feed {
    width: 60%;
    margin-left: 25%;
}

.feed-carroussel {
    margin-top: 70px;
    width: 60%;
    max-width: 600px;
}

.feed-fotos {
    max-width: 600px;
    height: 500px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.feed-post {
    border-width: 1px grey;
    border-style: outset;
    width: 605px;
    box-sizing: unset;
    padding-left: 4px;
}

.suggestions {
    max-width: 30%;
    position: fixed;
    margin-right: 50%;

}

.prof-photo-sugest {
    margin-top: 70px;
    max-width: 360px;
    max-height: 60px;
}

.footer {
    margin-top: 60%;
    max-width: 360px;
}

.footer-list ul li {
    list-style: none;
    display: inline-block;
    padding: 1px;
    font-size: 14px;
    color: #C7C7C7;
    text-align: justify;
}

.rightsfooter {
    padding-top: 15px;
    font-size: 14px;
    color: #C7C7C7;
}